import { shape, string } from 'prop-types';
import React from 'react';
import { useSelector } from 'react-redux';

import { Layout, SEO } from 'components';

const Job = ({ pageContext: { id } }) => {
  const { title, description, company, employment_type } = useSelector(
    ({ jobs }) => jobs.find(job => job.id === id)
  );

  return (
    <Layout>
      <SEO title={title} description={description} />
      <h1>{title}</h1>
      <dl>
        <dt>Description</dt>
        <dd>{description}</dd>
      </dl>
      <dl>
        <dt>Company</dt>
        <dd>{company}</dd>
      </dl>
      <dl>
        <dt>Type</dt>
        <dd>{employment_type}</dd>
      </dl>
    </Layout>
  );
};

Job.propTypes = {
  pageContext: shape({
    id: string.isRequired,
  }).isRequired,
};

Job.defaultProps = {};

export default Job;
